import React from 'react';
import CountUp from 'react-countup';

const Cards = (props) => {
    // console.log(props.info);
    if (props.info) {
        // console.log(props.info.active);

        console.log(props);
        // console.log(props.active); console.log(props.rcd);
    }

    return (
        <div className="container rsp">

            {props.info
                ? <div className="dwn rsp">
                        <div className="d-flex justify-content-center mb-0">

                            <div className="text-center">
                                <ul className="text-dark">
                                    <li className="alert alert-danger text-capitalize ">active : &nbsp; &nbsp;
                                        <span className="card-text fnt">{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.info.active
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}
                                        </span>&nbsp; &nbsp;
                                        <span>
                                            (&nbsp;{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.active
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}&nbsp;)</span>

                                    </li>
                                    <li className="alert alert-primary text-capitalize">deaths : &nbsp; &nbsp;
                                        <span className="card-text fnt">{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.info.deaths
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}</span>&nbsp; &nbsp;
                                        <span className="">(&nbsp;{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.dead
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}&nbsp;)</span>
                                    </li>
                                    <li className="alert alert-success text-capitalize">recovered : &nbsp;
                                        <span className="card-text fnt">{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.info.recovered
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}</span>
                                        &nbsp; &nbsp;
                                        <span className="card-text ">(&nbsp;{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                props.rcd
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}&nbsp;)</span>
                                    </li>
                                    <li className="alert alert-warning text-capitalize">last updated on :
                                        <p className="text-capitalize mb=0 pb-0">
                                            not available</p>
                                    </li>

                                </ul>
                            </div>
                        </div>
                    </div>
                : null}

        </div>
    );
}

export default Cards;
